* {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
  line-height: 1.5;
  box-sizing: border-box;
}

.navbar {
  display: flex;
  padding: 10px 25px;

  background-image: linear-gradient(
    to right,
    rgb(233, 57, 80),
    rgba(238, 18, 18, 0.801)
  );
  box-sizing: inherit;
}

.logo {
  font-size: 30px;
  text-transform: uppercase;
  color: #fff;
  font-weight: 900;
  letter-spacing: 3px;
}

.menu {
  font-weight: 300;
  font-size: 20px;
  margin-left: 10px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
}

.users-container {
  max-width: 900px;
  margin: 10px auto;
}

.users-container img {
  width: 100px;
  border-radius: 100%;
  padding: 10px;
}

.users {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 10px;
}

.user {
  text-align: center;
  background-color: #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  padding: 20px 5px;
}

.user h1 {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 400;
  color: #777777;
  padding: 5px 0;
}

.user .btn {
  padding: 0.3rem 1rem;
  font-size: 15px;
  display: inline-block;
  text-transform: uppercase;
  background-color: rgb(233, 57, 80);
  color: #fff;
  border: none;
  cursor: pointer;
  text-decoration: none;
  transition: 0.5s all;
  margin: 20px 0;
}

.user .btn:active,
.user .btn:focus {
  outline: none;
}

.user .btn:hover {
  opacity: 0.8;
}

.seacrh {
  margin: 10px auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.seacrh input {
  flex: 0 0 40%;
  padding: 0.4rem 10px;
}

.seacrh input::placeholder {
  font-size: 16px ;
  font-weight: 400;
  font-family: inherit !important;
  color: #777777 !important;
}
.seacrh button {
  padding: .4rem 10px;
  background-color:#da190b;
  border: none;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
  font-weight: 300;
  font-size: 16px;
  text-transform: uppercase;
  color: #fff;
}

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

aside {
  width: 100%;
  display: block;
  text-align: center;
  margin: 20px 0;
  font-size: 18px;
  color: #fff;
  text-transform: uppercase;
  background-color: rgba(218, 213, 213, 0.959);
  cursor: pointer;
  user-select: none;
}

aside:hover {
  opacity: 0.7;
}

.navbar ul {
  margin-left: auto;

  display: flex;
  list-style: none;
}

.navbar ul li {
  padding: 0 10px;
}
.navbar ul li a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 400;
  color: #fff;
}

.pills .pill {
  padding: 0.2rem 0.6rem;
  text-transform: uppercase;
  color: #fff;
  font-size: 12px;
  margin: 0 4px;
}

.pills .pill strong {
  margin-right: 3px;
}

.success {
  background-color: #4caf50;
} /* Green */
.success:hover {
  background-color: #46a049;
}

.info {
  background-color: #2196f3;
} /* Blue */
.info:hover {
  background: #0b7dda;
}

.warning {
  background-color: #ff9800;
} /* Orange */
.warning:hover {
  background: #e68a00;
}

.danger {
  background-color: #f44336;
} /* Red */
.danger:hover {
  background: #da190b;
}

.default {
  background-color: #e7e7e7;
  color: black;
} /* Gray */
.default:hover {
  background: #ddd;
}

.container-2 {
  max-width: 800px;
  margin: 20px auto;
  background-color: #fff;
  padding: 10px;
}

.container-3 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.col-right {
  text-align: center;
  padding: 20px 4px;
}
.user-img {
  width: 200px;
  border-radius: 100%;
}

.name {
  text-transform: uppercase;
  font-size: 20px;
  color: #777777;
}

.col-left {
  padding: 30px 10px;
}

.col-left ul {
  list-style: none;
  margin-top: 10px;
}
.bio {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.bio-heading {
  color: #777777;
  font-size: 20px;
}

.bio-btn {
  text-decoration: none;
  padding: 5px 10px;
  text-transform: uppercase;
  background-color: #da190b;
  color: #fff;
  margin-top: 30px;
}

.location {
  font-size: 16px;
  color: #777777;
}

.pill-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-bottom: 40px;
  user-select: none;
}
.alert {
  color: #fff;
  font-size: 20px;
  width: 50%;
  margin: 20px auto;
display: flex;
justify-content: center;
align-items: center;
padding: 5px 50px;
user-select: none;
}
.alert span {
}
.alert i {
  margin-right:10px;
}

.backBtn {
  text-decoration: none;
  padding: 3px 5px;
  margin: 10px;
  text-transform: uppercase;
  background-color: #da190b;
  color: #fff;
  display: inline-block;
  padding: 2px 10px;
}
